import {  createWebHistory,createRouter } from "vue-router";
import List from './components/List';
import Home from './components/Home';
import Detail from './components/Detail';
import Author from './components/Author';
import Comment from './components/Mention';

const routes = [
  {
    path: "/list",
    component: List,
  },
  {
    path: "/",
    component: Home,
    /*
    **named views : 여러개 컴포넌트 보여줄 때 (조립식)
     component :{
        List : List,
        Comment : Comment,
    }
    <router-view>항목도 여러개 사용해야합니다.
    */
  },
  {
    path: "/detail/:id", //정규식가능 (\\d+) ,(/:id* 하면 /:id/:id/:id)
    component: Detail,
    // beforeEnter: () => {
    //     if(로그인==false){
    //         return '/login'
    //     }
    // }
    children: [
        {
            path: "author",
            component: Author,
        },
        {
            path: "comment",
            component: Comment,
        },
    ]
  },
  {
    path: "/:anyting(.*)", //정규식가능 (/:id* 하면 /:id/:id/:id)
    component: Detail,
  },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router; 